import request from '../HttpRequest'
import keyBy from 'lodash/fp/keyBy';

const keyById = keyBy('id');

function EventClass() {
  let loadedEvents;

  function getEvents() {
    return request.get('/events')
      .then(keyById)
      .catch((e) => {
        console.error(e);
        return Promise.reject(e);
      });
  }

  function getEventById(id) {
    return request.get('/events/' + id)
      .catch((e) => {
        console.error(e);
        return Promise.reject(e);
      });
  }

  function saveEvent(data) {
    if (data.id) {
      return request.put('/events/' + data.id, data)
        .catch((e) => {
          console.error(e);
          return Promise.reject(e);
        });
    }

    return request.post('/events', data)
      .catch((e) => {
        console.error(e);
        return Promise.reject(e);
      });
  }

  function removeEventById(id) {
    return request.delete('/events/' + id)
      .catch((e) => {
        console.error(e);
        return Promise.reject(e);
      });
  }

  function getFlatEvents(data){
    return request.get('/events/' + 'range?from='+ data.start + '&to=' + data.end)
      .catch((e) => {
        console.error(e);
        return Promise.reject(e);
      });
  }


  this.getAll = getEvents;

  this.saveEvent = saveEvent;

  this.removeEventById = removeEventById;

  this.getById = getEventById;

  this.getFlatEvents = getFlatEvents;

}

export default EventClass



