<template>
  <modal :on-close="close">
    <h3 slot="header">Opprett event</h3>
    <div class="modal_content modal_content__scroller">
      <event-form :value="event" v-on:submit="submitForm"></event-form>
    </div>
  </modal>
</template>


<script>
  import clone from 'lodash/fp/clone';
  import router from '../router'
  import Modal from './Modal'
  import EventForm from './EventForm'

  export default {
    components: {
      Modal,
      EventForm
    },
    computed: {
      event () {
        return {...this.$store.state.newEvent, createdBy: this.$store.state.user.id}
      }
    },
    methods: {
      close: function () {
        router.push({name: 'date', params: this.$route.params})
      },
      submitForm(event) {
        this.$store.dispatch('saveEvent', event).then((event)=>{
          router.push({ name: 'viewEvent', params: Object.assign({}, this.$route.params, {id: event.id}) })
        })
      }
    }
  }
</script>



