<script>
  import Vue from 'vue'
  import store from '../store'
  import find from 'lodash/find'
  import memoize from 'lodash/memoize';


  const getThemeOfCategory = memoize(function (categoryId) {
    var category = find(store.state.categories, {id: categoryId});
    if (!category) {
      return false;
    }
    if (!category.theme && category.parentId != category.id) {
      return getThemeOfCategory(category.parentId);
    }
    return category.theme || false;
  });


  export default Vue.directive('ThemeByCategory', function (el, binding) {
    if (binding.value === binding.oldValue) {
      return;
    }

    el.className = el.className.replace(/(\s|^)(grey-black|grey-blue|blue-red)(\s|$)/,'');

    const theme = getThemeOfCategory(binding.value);
    if (!theme) {
      return;
    }

    const regex = new RegExp("(?:^|\s)" + theme + "(?!\S)");
    if (!el.className.match(regex)) {
      el.className += " " + theme;
    }
  })
</script>



